@keyframes drop-in {
  from {
    transform: translateY(-500%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.hover path {
  stroke: #ccc;
}

.hover text {
  fill: #ccc;
}

.hover g.primary text {
  fill: black;
  font-weight: bold;
}

.hover g.secondary text {
  fill: #333;
}

.hover path.primary {
  stroke: #333;
  stroke-opacity: 1;
}

.animated * {
  transition: all 1s ease-in-out, stroke 0.125s ease-in-out, stroke-opacity 0.125s ease-in-out;
}

.animated .drop {
  animation-name: drop-in;
  animation-duration: 0.5s;
}
