@keyframes drop-in {
  from {
    transform: translateY(-500%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes fade-out {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

.hover rect {
  fill-opacity: 0.125;
}

.hover rect.primary {
  fill-opacity: 0.75;
}

.hover rect.secondary {
  fill-opacity: 0.5;
}

.series-filled {
  fill-opacity: 0.5;
}

.animated {
  transition: all 0.5s ease-in-out, opacity 0.125s ease-in-out;
}

.animated.entering {
  animation-name: drop-in;
  animation-duration: 0.5s;

  transition: all 0.5s ease-in-out, opacity 0.125s ease-in-out;
}
